module.exports = "import { Component } from 'react';\nimport Trigger from '../Trigger';\nimport Radio, { RadioGroup } from '../../radio';\n\nconst PLACEMENT_ENUM = {\n  left: {\n    points: ['cr', 'cl'],\n  },\n  right: {\n    points: ['cl', 'cr'],\n  },\n  top: {\n    points: ['bc', 'tc'],\n  },\n  bottom: {\n    points: ['tc', 'bc'],\n  },\n  topLeft: {\n    points: ['bl', 'tl'],\n  },\n  topRight: {\n    points: ['br', 'tr'],\n  },\n  bottomRight: {\n    points: ['tr', 'br'],\n  },\n  bottomLeft: {\n    points: ['tl', 'bl'],\n  },\n};\n\nconst ActionType = [\n  'hover',\n  'click',\n];\n\nexport default class TriggerDemo extends Component {\n  state = {\n    placement: 'bottom',\n    action: 'click',\n  }\n  onChangePlacement = (e) => {\n    this.setState({\n      placement: e.target.value,\n    });\n  }\n  onChangeActionType = (e) => {\n    console.log(e.target.value)\n    this.setState({\n      action: e.target.value,\n    });\n  }\n  renderPlacementSelector() {\n    const { placement } = this.state;\n    return (\n      <select value={placement} onChange={this.onChangePlacement}>\n        {\n          Object.keys(PLACEMENT_ENUM).map(p => (\n            <option key={p}>{p}</option>\n          ))\n        }\n      </select>\n    );\n  }\n  render() {\n    const {\n      placement,\n      action,\n    } = this.state;\n    return (\n      <div>\n        <label htmlFor=\"placement\">对齐方式</label>\n        {\n          this.renderPlacementSelector()\n        }\n        <label htmlFor=\"action\">触发方式</label>\n        <RadioGroup value={action} onChange={this.onChangeActionType}>\n          {\n            ActionType.map(type => (\n              <Radio\n                value={type}\n                key={type}\n              >{type}</Radio>\n            ))\n          }\n        </RadioGroup>\n        <Trigger\n          action={action}\n          popup={\n            <div style={{ border: '1px solid #000', padding: 10, background: '#fff' }}>popup content</div>\n          }\n          placement={PLACEMENT_ENUM[placement].points}\n          mouseLeaveDelay={100}\n        >\n          {`${action} me`}\n        </Trigger>\n      </div>\n    );\n  }\n}\n"


