module.exports = "import Modal from '../Modal';\nimport Button from 'quark-ui/button';\nimport { Component } from 'react';\n\nexport default class ModalDemo extends Component {\n  constructor(props) {\n    super(props);\n    this.openModal = this.openModal.bind(this);\n    this.state = {\n      visible: false,\n    };\n  }\n  openModal() {\n    this.setState({\n      visible: true,\n    });\n  }\n  closeModal() {\n    this.setState({\n      visible: false,\n    });\n  }\n  render() {\n    const { visible } = this.state;\n    const modalProps = {\n      title: '标题',\n      visible,\n      onOk: () => { this.closeModal(); },\n      onCancel: () => { this.closeModal(); },\n    };\n    return (\n      <div>\n        <h3>基本</h3>\n        <Button type=\"secondary\" onClick={this.openModal}>open modal</Button>\n        <Modal {...modalProps}>\n          <p>这是一段信息。</p>\n        </Modal>\n        <h3>信息提示</h3>\n        <p>各种类型的信息提示，只提供一个按钮用于关闭。</p>\n        <Button\n          type=\"secondary\"\n          onClick={() => {\n            Modal.info({\n              content: '这是提示信息',\n              closable: true,\n            });\n          }}\n        >info</Button>\n        <Button\n          type=\"secondary\"\n          onClick={() => {\n            Modal.success({\n              content: '这是成功消息',\n            });\n          }}\n        >success</Button>\n        <Button\n          type=\"secondary\"\n          onClick={() => {\n            Modal.error({\n              content: '这是错误提示',\n            });\n          }}\n        >error</Button>\n        <Button\n          type=\"secondary\"\n          onClick={() => {\n            Modal.warning({\n              content: '这是警告信息',\n            });\n          }}\n        >warning</Button>\n      </div>\n    );\n  }\n}\n"


