module.exports = "import InputNumber from '../InputNumber';\nimport { Component } from 'react';\nimport CSSModules from 'react-css-modules';\nimport styles from '../../../styles/demo.css';\n\n\n@CSSModules(styles)\n\nexport default class InputNumberDemo extends Component {\n\n  onChange = (value) => {\n    console.log('changed', value);\n  }\n\n  render() {\n    return (\n      <div>\n        <section styleName=\"code__box\">\n          <div styleName=\"code__temp\">\n            <span>基本</span>\n            <p>数字输入框</p>\n            <div>\n              <InputNumber\n                style={{ width: 200 }}\n                min={1}\n                max={10}\n                defaultValue={3}\n                onChange={this.onChange}\n              />\n            </div>\n          </div>\n          <div styleName=\"code__temp\">\n            <span>禁用</span>\n            <p>数字输入框禁用</p>\n            <div>\n              <InputNumber min={1} max={10} disabled defaultValue={3} />\n            </div>\n          </div>\n          <div styleName=\"code__temp\">\n            <span>小数</span>\n            <p>和原生的数字输入框一样，鼠标离开输入框时自动取值。目前设定小数位两位。</p>\n            <div>\n              <InputNumber min={0} max={10} defaultValue={3} step={0.11} onChange={this.onChange} />\n            </div>\n          </div>\n          <div styleName=\"code__temp\">\n            <span>大小</span>\n            <p>三种大小的数字输入框。</p>\n            <div>\n              <InputNumber size=\"large\" min={1} max={100000} defaultValue={3} onChange={this.onChange} />\n              <InputNumber min={1} max={100000} defaultValue={3} onChange={this.onChange} />\n              <InputNumber size=\"small\" min={1} max={100000} defaultValue={3} onChange={this.onChange} />\n            </div>\n          </div>\n          <div styleName=\"code__temp\">\n            <span>格式化展示</span>\n            <p>展示具有具体含义的数据</p>\n            <div>\n              <InputNumber\n                formatter={value => `$ ${value.replace(/\\B(?=(\\d{3})+(?!\\d))/g, ',')}`}\n                parser={value => value.replace(/\\$\\s?|(,*)/g, '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1000}\n                formatter={value => `¥ ${value.replace(/\\B(?=(\\d{3})+(?!\\d))/g, ',')}`}\n                parser={value => value.replace(/\\¥\\s?|(,*)/g, '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}m`}\n                parser={value => value.replace('m', '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}㎡`}\n                parser={value => value.replace('㎡', '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}㎝`}\n                parser={value => value.replace('㎝', '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}㎞`}\n                parser={value => value.replace('㎞', '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}℅`}\n                parser={value => value.replace('℅', '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}℃`}\n                parser={value => value.replace('℃', '')}\n                onChange={this.onChange}\n              />\n              <InputNumber\n                defaultValue={1}\n                min={0}\n                max={10}\n                formatter={value => `${value}㎎`}\n                parser={value => value.replace('㎎', '')}\n                onChange={this.onChange}\n              />\n            </div>\n          </div>\n        </section>\n      </div>\n    );\n  }\n}\n"


