module.exports = "import { Component } from 'react';\nimport moment from 'moment';\nimport DatePicker from '../index';\nimport Checkbox from '../../checkbox';\n\nconst { MonthPicker, RangePicker } = DatePicker;\n\nexport default class DatePickerDemo extends Component {\n  state = {\n    disabled: false,\n    date: moment().add(1, 'M'),\n  }\n  onChange(m) {\n    console.log(m);\n  }\n  changeDisabled = () => {\n    this.setState({\n      disabled: !this.state.disabled,\n    });\n  }\n  render() {\n    const { date, disabled } = this.state;\n    return (\n      <div>\n        <Checkbox\n          checked={disabled}\n          onChange={this.changeDisabled}\n        >\n          禁用\n        </Checkbox>\n        <h2>日期选择</h2>\n        <h3>非受控方式</h3>\n        <DatePicker\n          disabled={disabled}\n          onChange={this.onChange}\n        />\n        <h3>受控方式</h3>\n        <DatePicker\n          disabled={disabled}\n          value={date}\n          onChange={(d) => {\n            this.setState({\n              date: d,\n            });\n          }}\n        />\n        <p>选择时间: {date.format()}</p>\n        <h2>不可选日期</h2>\n        <p>可用 disabledDate 禁止选择部分日期</p>\n        <DatePicker\n          disabled={disabled}\n          disabledDate={(current) => {\n            return current && current.valueOf() < Date.now();\n          }}\n        ></DatePicker>\n        <h2>月份选择</h2>\n        <MonthPicker onChange={this.onChange} disabled={disabled} />\n        <h2>预设范围</h2>\n        <p>RangePicker 可以设置常用的 预设范围 提高用户体验。</p>\n        <RangePicker onChange={this.onChange} disabled={disabled} />\n      </div>\n    );\n  }\n}\n"


